.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: $calendar-gap;
  padding: 20px $calendar-padding;
  width: $calendar-width;

  &__day {
    position: relative;
    background-color: #eee;
    height: $day-size;
    width: $day-size;
    border: 1px solid black;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    cursor: pointer;
    transition-property: background-color, transform;
    transition-duration: 0.5s;

    &:hover {
      background-color: pink;
      transform: translateY(-20px);
    }

    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

  }

  @for $day from 1 through  31 {
    &__day:nth-child(n + #{$day})::before {
      content: "#{$day}";
    }
  }

  @each $day, $index  in $week {
    &--start--day--#{$day} :first-child {
      margin-left: $index;
    }
  }

  @for $index from 28 through 31 {
    &--month-length--#{$index} :nth-child(n + #{$index + 1}) {
      display: none;
    }
  }

}
